<template>
  <div class="sure-order child-view">
    <header-pub headerTitle=""></header-pub>
    <!--  -->
    <a class="sure-goods" v-if="JSON.stringify(this.goodsInfo)!='{}'">
      <div class="img-box">
        <img :src="goodsInfo.main_picture">
      </div>
      <div class="info-box">
        <h2 class="name">{{goodsInfo.conference_name}}</h2>
        <div class="tips">x{{goodsInfo.counts}}&nbsp;&nbsp;{{goodsInfo.price_title}}</div>
        <div class="price">{{goodsInfo.original_price*goodsInfo.counts}}</div>
      </div>
    </a>
    <!--  -->
    <div class="sure-address card-box">
      <div class="title">
        <h2></h2>
      </div>
      <div class="adress-box info-box" v-if="optionShoppingAddress!=null">
        <div class="address-1">
          <span class="name"></span>
          <span class="pho">13813007120</span>
          <div class="i-trans"><span class="trans"></span></div>
        </div>
        <div class="address-2">
          <i class="location"></i>
          <p></p>
        </div>
      </div>
      <div class="add-box"  v-if="optionShoppingAddress==null">
        <i class="icon iconfont icon-tianjia"></i>{{addAddress}}
      </div>
    </div>
     <!--  -->
    <div class="sure-fp card-box">
      <div class="title">
        <h2></h2>
        <div class="fp-need-btn">
          <mt-switch v-model="needFp"></mt-switch>
        </div>
      </div>
      <div class="info-fp"  v-if="optionInvoice!=null" :class="{need: needFp}">
        <div class="fp-box info-box">
          <div class="fp-1">
            <span class="name">xx</span><div class="i-trans"><span class="trans"></span></div>
          </div>
          <div class="fp-2">
            <i class="email"></i>
            <p>995107408@qq.com</p>
          </div>
          <div class="fp-type fp-type-1"></div> <!-- fp-type-1  fp-type-2   -->
        </div>
      </div>
      <div class="add-box info-fp"  v-if="optionInvoice==null"  :class="{need: needFp}">
        <i class="icon iconfont icon-tianjia"></i>{{addAddress}}
      </div>
    </div>
     <!--  -->
    <div class="sure-zf card-box">
      <div class="title">
        <h2></h2>
      </div>
      <div class="zf-list" :class="`zf-type-${chooseZfType}`">
        <mt-radio
          v-model="chooseZfType"
          :max="1"
          align="right"
          :options="optionsZf">
        </mt-radio>
      </div>
    </div>
    <!-- fix -->
    <div class="sure-btn">
      <div class="sum-box"><span class="sum">{{goodsInfo.original_price*goodsInfo.counts}}</span></div>
      <div class="submit-btn" @click="ConfirmPay"></div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import { needMixin } from 'common/js/mixin'
import HeaderPub from 'base/header/header-pub'
import { Toast }  from 'mint-ui'
import { ConfirmConferenceOrder,CreateOrder } from '@/api/api.js'
export default {
  mixins: [needMixin],
  components: {
    HeaderPub
  },
  data () {
    return {
      needFp: false, // 
      chooseZfType: '1', // 
      addAddress:'',
      addInvoice:'',
      goodsInfo:{}, //
      optionUserInfo:{},//
      optionInvoice:{}, //
      optionShoppingAddress:{}, //
      optionsZf: [ //  
        {
          label: '',
          value: '1'
        }
//        {
//          label: '',
//          value: '2'
//        },
//        {
//          label: '',
//          value: '3'
//        }
      ]
    }
  },
  created () {
    this._getAllData()
  },
  methods: {
    _getAllData () {
      this.optionUserInfo=JSON.parse(sessionStorage.getItem("user"))
      this.goodsInfo=this.$route.query
      console.log(this.optionUserInfo.user_token)
      let promise1 = new Promise((resolve, reject) => {
        ConfirmConferenceOrder(this.optionUserInfo.user_token).then((res) => {
          if (res.result === true && res.dataList) {
            this.optionInvoice=res.dataList.invoice
            this.optionShoppingAddress=res.dataList.shopping_address
          } else {
//            this.toast('')
//            setTimeout(() => {
//              this.$router.go(-1)
//            }, 2000)
          }
        })
      })
    },
    ConfirmPay () {
      if(this.needFp==true&&this.optionShoppingAddress==null){
        Toast({
          message: '',
          duration: 1000
        });
        return
      }
      if(this.needFp==true&&this.optionInvoice==null){
        Toast({
          message: '',
          duration: 1000
        });
        return
      }
      if(this.needFp==false){
        var query=this.goodsInfo
//        token   conference_id price_id counts remark address_id invoice_id pay_type
        this.goodsInfo.token=this.optionUserInfo.user_token
        this.goodsInfo.pay_type=2
        console.log(this.goodsInfo)
        CreateOrder(query).then((res) => {
          if (res.result === false && res.dataList==null) {
            console.log(222)
            this.toast({
              message:res.message,
              duration: 3000
            })
            return
          }
          if (res.result === true && res.dataList==null) {
            this.$router.push({path: '/finish'})
            return
          }
          if(res.result === true && res.dataList!=null){
            this.$router.push({path: '/payment',query:res.dataList})
            return
          }
//          else {
////            this.toast('')
////            setTimeout(() => {
////              this.$router.go(-1)
////            }, 2000)
//          }
        })
      }
//      Toast({
//        message: '',
//        duration: 5000
//      });
    }
  },
  watch: {
  }
}
</script>
<style lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/mixin"
// mint swich 
.mint-switch-input:checked + .mint-switch-core::before
  transform: none
.mint-switch-core::before
  content: ''
  text-align: left
  line-height: 32px
  color: #999
  text-indent: 65px
  background: 50% 50%/100% 100% no-repeat
  bg-image("~common/images/fp-swich")
.mint-switch-core,.mint-switch-core::before
  width: 100px
  border: none
  background-color: #f7f7f7
  height: 32px
.mint-switch-core::after
  height: 32px
  width: 50px
  border-radius: 16px
  box-shadow: none
  background: #999999
  transition: all .3s
.mint-switch-input:checked + .mint-switch-core::after
  transform: translateX(50px)
  background: #f53e3e
.mint-switch-input:checked + .mint-switch-core
  background-color: #f7f7f7
// 
.zf-list
  .mint-cell-wrapper
    background: #fff
    padding: 0 0
    .mint-radiolist-label
      padding: 0 10px 0 0
      .mint-radio-label
        display: block
        height: 40px
        padding-left: 40px
        line-height: 40px
        color: #808080
      .mint-radio
        margin-top: 10px
  .mint-cell:first-of-type
    .mint-radio-label
      background: url("~common/images/wx.png") left center/auto 30px no-repeat
  .mint-cell:nth-of-type(2)
    .mint-radio-label
      background: url("~common/images/zfb.png") 2px center/auto 28px no-repeat
  .mint-cell:nth-of-type(3)
    .mint-radio-label
      background: url("~common/images/gdg.png") 2px center/28px auto no-repeat
  .mint-radio-input:checked + .mint-radio-core
    background: #f53e3e
    border-color: #f53e3e
//  
.zf-type-1
  .mint-cell:first-of-type
    .mint-radio-label
      color: #333
.zf-type-2
  .mint-cell:nth-of-type(2)
    .mint-radio-label
      color: #333
.zf-type-3
  .mint-cell:nth-of-type(3)
    .mint-radio-label
      color: #333
</style>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/mixin"
.sure-order
  padding-top: 52px
  padding-bottom: 52px
  .sure-goods
    height: 119px
    display: flex
    padding: 10px 12px
    background: #fff right 12px center/auto 12px no-repeat
    bg-image("~common/images/goto")
    .img-box
      width: 100px
      height: 100px
      border: 1px solid #e5e5e5
      margin-right: 10px
      img
        width: 100%
        height: 100%
    .info-box
      flex: 1
      width: 0
      position: relative
      .name
        font-size: 16px
        line-height: 1.7
        letter-spacing: 0.5px
        overflow: hidden
        no-wrap()
      .price
        position: absolute
        bottom: 4px
        color: #ff3636
        font-size: 14px
        letter-spacing: .5px
      .tips
        position: absolute
        bottom: 24px
        line-height: 1.4
        letter-spacing: .5px
        color: #adadad
  .card-box
    padding: 0 12px
    margin-top: 10px
    background: #fff
    .title
      height: 38px
      position: relative
      h2
        line-height: 38px
        font-size: 16px
    .info-box
      position: relative
      padding: 12px 0
      &:after
        line-scale()
        top: 0
        bottom: auto
      .i-trans
        width: 36px
        height: 16px
        display: inline-block
        position: relative
        background: #ff3636
        border-radius: 3px
        overflow: hidden
        margin-left: 5px
        vertical-align: middle
        margin-top: -1px
        span.trans
          line-height: 1
          display: block
          width: 72px
          height: 32px
          font-size: 24px
          text-align: center
          line-height: 32px
          transform: scale(.5)
          transform-origin: 0 0
          background: #ff3636
          color: #fff
  .sure-address
    .adress-box
      .address-1
        padding: 4px 0
        span
          line-height: 22px
          font-size: 16px
          vertical-align: middle
        .name
          margin-right: 25px
      .address-2
        display: flex
        margin-top: 10px
        .location
          width: 16px
          height: 16px
          margin-right: 5px
          color: #666
          background: url("~common/images/locat.png") 50% 50%/auto 16px no-repeat
        p
          flex: 1
          width: 0
          line-height: 1.5
  .sure-fp
    .title
      position: relative
      .fp-need-btn
        position: absolute
        right: 0
        top: 3px
    .info-fp
      position: relative
      height: 0px
      overflow: hidden
      transition: height 0.3s ease-out
      &.need
        height: 82px
    .fp-box
      .fp-1
        padding: 4px 0
        .name
          display: inline-block
          max-width: 300px
          line-height: 22px
          font-size: 16px
          vertical-align: middle
          no-wrap()
      .fp-2
        display: flex
        margin-top: 10px
        .email
          width: 16px
          height: 16px
          margin-right: 5px
          color: #666
          background: url("~common/images/locat.png") 50% 50%/auto 16px no-repeat
        p
          flex: 1
          width: 0
          line-height: 1.5
          no-wrap()
    .fp-type
      position: absolute
      right: 0
      bottom: 10px
      width: 50px
      height: 25px
      background: 50%/100% no-repeat
      &.fp-type-1
        bg-image("~common/images/fp-type-1")
      &.fp-type-2
        bg-image("~common/images/fp-type-2")
  .sure-btn
    position: fixed
    bottom: 0
    height: 42px
    width: 100%
    max-width: 750px; /*no*/ //  rem
    z-index: 10
    display: flex
    background: #fff
    box-shadow: 0 0 5px #f7ebeb
    .sum-box
      flex: 1
      width: 0
      text-align: center
      line-height: 42px
      font-size: 14px
      white-space:nowrap
      .sum
        font-size: 16px
        color: #ff3636
    .submit-btn
      text-align: center
      background: #ff3636
      line-height: 42px
      color: #fff
      font-size: 16px
      letter-spacing: 0.5px
      width: 185px

.add-box
  width:100%
  margin:0 auto
  text-align: center
  color: #FF3636
  font-size:0.42rem
  padding:12px 0
  border-top:1px solid #E4E4E4
  i
    font-size:0.38rem
    margin-right: 5px

.sure-order .sure-fp .info-fp.add-box
  padding:0
  color:#fff
.sure-order .sure-fp .info-fp.add-box.need
  padding:0
  color: #FF3636
  line-height: 82px
</style>



